    font-size: 30px;
    font-weight: 500;
    margin-left: 13%;

}
.message form{
    width:50%;
    margin-left: 5%;
    display: flex;
    text-align: left;
    flex-direction: column;
}

.message form input{
    margin-top: 10px;
    margin-left: 40%;
    border-radius: 7px;
    height:25px;
    width: 80%;
  
}
textarea{
    margin-top: 10px;
    margin-left: 40%;
    border: 2px solid black;
    border-radius: 7px ;
    height:100px;
    width: 80%;
}

textarea[placeholder]{
    padding-left: 5%;
}
.message p:nth-child(5){
    margin-left: 25%;
}

.message form input[placeholder]{
    padding-left: 5%;
   
}


.design{
